import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  Card,
  CardContent,
  Grid,
  Alert,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  IconButton,
  Chip,
  CircularProgress,
} from '@mui/material';
import {
  Inventory as InventoryIcon,
  Add,
  Edit,
  Delete,
  Warning,
  Refresh,
} from '@mui/icons-material';
import { inventoryAPI, productAPI } from '../services/api';
import { useAuthStore } from '../store/authStore';
import { Product } from '../types';
import { formatCurrency } from '../utils/formatters';
import toast from 'react-hot-toast';

const Inventory: React.FC = () => {
  const { user } = useAuthStore();
  const [dashboardData, setDashboardData] = useState<any>(null);
  const [products, setProducts] = useState<Product[]>([]);
  const [loading, setLoading] = useState(true);
  const [showAddProduct, setShowAddProduct] = useState(false);
  const [newProduct, setNewProduct] = useState<Partial<Product>>({
    name: '',
    barcode: '',
    plu: '',
    category: 'pantry',
    price: 0,
    priceType: 'fixed',
    unit: 'piece',
    stockQuantity: 0,
    lowStockThreshold: 10,
    reorderPoint: 5,
    taxable: true,
    taxRate: 0.0875,
    ageRestricted: false,
    minimumAge: 18,
    isActive: true,
  });

  const categories = [
    'produce', 'dairy', 'meat', 'bakery', 'frozen', 'pantry', 
    'beverages', 'household', 'pharmacy', 'alcohol', 'tobacco'
  ];

  const fetchDashboard = async () => {
    try {
      const response = await inventoryAPI.getDashboard();
      setDashboardData(response.data);
    } catch (error: any) {
      console.error('Error fetching dashboard:', error);
      toast.error('Failed to load inventory dashboard');
    }
  };

  const fetchProducts = async () => {
    try {
      const response = await productAPI.getAll({ limit: 100 });
      setProducts(response.data.products);
    } catch (error: any) {
      console.error('Error fetching products:', error);
      toast.error('Failed to load products');
    }
  };

  const loadData = async () => {
    setLoading(true);
    await Promise.all([fetchDashboard(), fetchProducts()]);
    setLoading(false);
  };

  useEffect(() => {
    if (user?.permissions.canManageInventory) {
      loadData();
    } else {
      setLoading(false);
    }
  }, [user]);

  const handleAddProduct = async () => {
    try {
      await productAPI.create(newProduct);
      toast.success('Product added successfully');
      setShowAddProduct(false);
      setNewProduct({
        name: '',
        barcode: '',
        plu: '',
        category: 'pantry',
        price: 0,
        priceType: 'fixed',
        unit: 'piece',
        stockQuantity: 0,
        lowStockThreshold: 10,
        reorderPoint: 5,
        taxable: true,
        taxRate: 0.0875,
        ageRestricted: false,
        minimumAge: 18,
        isActive: true,
      });
      await loadData();
    } catch (error: any) {
      const message = error.response?.data?.message || 'Failed to add product';
      toast.error(message);
    }
  };

  const handleDeleteProduct = async (productId: string) => {
    if (window.confirm('Are you sure you want to delete this product?')) {
      try {
        await productAPI.delete(productId);
        toast.success('Product deleted successfully');
        await loadData();
      } catch (error: any) {
        const message = error.response?.data?.message || 'Failed to delete product';
        toast.error(message);
      }
    }
  };

  if (!user?.permissions.canManageInventory) {
    return (
      <Box>
        <Typography variant="h4" gutterBottom>
          Inventory Management
        </Typography>
        <Alert severity="warning">
          You don't have permission to access inventory management features.
        </Alert>
      </Box>
    );
  }

  if (loading) {
    return (
      <Box display="flex" justifyContent="center" alignItems="center" minHeight="400px">
        <CircularProgress />
      </Box>
    );
  }

  return (
    <Box>
      <Box display="flex" justifyContent="space-between" alignItems="center" mb={3}>
        <Typography variant="h4">
          Inventory Management
        </Typography>
        <Box>
          <Button
            variant="outlined"
            startIcon={<Refresh />}
            onClick={loadData}
            sx={{ mr: 2 }}
          >
            Refresh
          </Button>
          <Button
            variant="contained"
            startIcon={<Add />}
            onClick={() => setShowAddProduct(true)}
          >
            Add Product
          </Button>
        </Box>
      </Box>

      <Grid container spacing={3}>
        <Grid item xs={12} sm={6} md={3}>
          <Card>
            <CardContent sx={{ textAlign: 'center' }}>
              <InventoryIcon sx={{ fontSize: 48, color: 'primary.main', mb: 2 }} />
              <Typography variant="h6" gutterBottom>
                Total Products
              </Typography>
              <Typography variant="h4" color="primary">
                {dashboardData?.summary?.totalProducts || 0}
              </Typography>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} sm={6} md={3}>
          <Card>
            <CardContent sx={{ textAlign: 'center' }}>
              <Warning sx={{ fontSize: 48, color: 'warning.main', mb: 2 }} />
              <Typography variant="h6" gutterBottom>
                Low Stock Items
              </Typography>
              <Typography variant="h4" color="warning.main">
                {dashboardData?.summary?.lowStockCount || 0}
              </Typography>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} sm={6} md={3}>
          <Card>
            <CardContent sx={{ textAlign: 'center' }}>
              <InventoryIcon sx={{ fontSize: 48, color: 'error.main', mb: 2 }} />
              <Typography variant="h6" gutterBottom>
                Out of Stock
              </Typography>
              <Typography variant="h4" color="error.main">
                {dashboardData?.summary?.outOfStockCount || 0}
              </Typography>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} sm={6} md={3}>
          <Card>
            <CardContent sx={{ textAlign: 'center' }}>
              <InventoryIcon sx={{ fontSize: 48, color: 'success.main', mb: 2 }} />
              <Typography variant="h6" gutterBottom>
                Total Value
              </Typography>
              <Typography variant="h4" color="success.main">
                {formatCurrency(dashboardData?.summary?.totalValue || 0)}
              </Typography>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                Product Inventory
              </Typography>
              <TableContainer component={Paper} variant="outlined">
                <Table>
                  <TableHead>
                    <TableRow>
                      <TableCell>Name</TableCell>
                      <TableCell>Category</TableCell>
                      <TableCell>Price</TableCell>
                      <TableCell>Stock</TableCell>
                      <TableCell>Status</TableCell>
                      <TableCell>Actions</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {products.map((product) => (
                      <TableRow key={product._id}>
                        <TableCell>
                          <Box>
                            <Typography variant="body2" fontWeight="medium">
                              {product.name}
                            </Typography>
                            {product.barcode && (
                              <Typography variant="caption" color="textSecondary">
                                Barcode: {product.barcode}
                              </Typography>
                            )}
                            {product.plu && (
                              <Typography variant="caption" color="textSecondary" display="block">
                                PLU: {product.plu}
                              </Typography>
                            )}
                          </Box>
                        </TableCell>
                        <TableCell>
                          <Chip label={product.category} size="small" />
                        </TableCell>
                        <TableCell>
                          {formatCurrency(product.price)}/{product.unit}
                        </TableCell>
                        <TableCell>
                          <Typography
                            color={
                              product.stockQuantity === 0
                                ? 'error'
                                : product.stockQuantity <= product.lowStockThreshold
                                ? 'warning.main'
                                : 'text.primary'
                            }
                          >
                            {product.stockQuantity}
                          </Typography>
                        </TableCell>
                        <TableCell>
                          <Chip
                            label={
                              product.stockQuantity === 0
                                ? 'Out of Stock'
                                : product.stockQuantity <= product.lowStockThreshold
                                ? 'Low Stock'
                                : 'In Stock'
                            }
                            color={
                              product.stockQuantity === 0
                                ? 'error'
                                : product.stockQuantity <= product.lowStockThreshold
                                ? 'warning'
                                : 'success'
                            }
                            size="small"
                          />
                        </TableCell>
                        <TableCell>
                          <IconButton size="small">
                            <Edit />
                          </IconButton>
                          <IconButton
                            size="small"
                            color="error"
                            onClick={() => handleDeleteProduct(product._id)}
                          >
                            <Delete />
                          </IconButton>
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </TableContainer>
            </CardContent>
          </Card>
        </Grid>
      </Grid>

      {/* Add Product Dialog */}
      <Dialog open={showAddProduct} onClose={() => setShowAddProduct(false)} maxWidth="md" fullWidth>
        <DialogTitle>Add New Product</DialogTitle>
        <DialogContent>
          <Grid container spacing={2} sx={{ mt: 1 }}>
            <Grid item xs={12} sm={6}>
              <TextField
                fullWidth
                label="Product Name"
                value={newProduct.name}
                onChange={(e) => setNewProduct({ ...newProduct, name: e.target.value })}
                required
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <FormControl fullWidth>
                <InputLabel>Category</InputLabel>
                <Select
                  value={newProduct.category}
                  onChange={(e) => setNewProduct({ ...newProduct, category: e.target.value })}
                  label="Category"
                >
                  {categories.map((cat) => (
                    <MenuItem key={cat} value={cat}>
                      {cat.charAt(0).toUpperCase() + cat.slice(1)}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                fullWidth
                label="Barcode"
                value={newProduct.barcode}
                onChange={(e) => setNewProduct({ ...newProduct, barcode: e.target.value })}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                fullWidth
                label="PLU Code"
                value={newProduct.plu}
                onChange={(e) => setNewProduct({ ...newProduct, plu: e.target.value })}
              />
            </Grid>
            <Grid item xs={12} sm={4}>
              <TextField
                fullWidth
                label="Price"
                type="number"
                value={newProduct.price}
                onChange={(e) => setNewProduct({ ...newProduct, price: parseFloat(e.target.value) || 0 })}
                inputProps={{ min: 0, step: 0.01 }}
                required
              />
            </Grid>
            <Grid item xs={12} sm={4}>
              <FormControl fullWidth>
                <InputLabel>Price Type</InputLabel>
                <Select
                  value={newProduct.priceType}
                  onChange={(e) => setNewProduct({ ...newProduct, priceType: e.target.value as 'fixed' | 'weight' })}
                  label="Price Type"
                >
                  <MenuItem value="fixed">Fixed Price</MenuItem>
                  <MenuItem value="weight">Price by Weight</MenuItem>
                </Select>
              </FormControl>
            </Grid>
            <Grid item xs={12} sm={4}>
              <FormControl fullWidth>
                <InputLabel>Unit</InputLabel>
                <Select
                  value={newProduct.unit}
                  onChange={(e) => setNewProduct({ ...newProduct, unit: e.target.value as any })}
                  label="Unit"
                >
                  <MenuItem value="piece">Piece</MenuItem>
                  <MenuItem value="lb">Pound</MenuItem>
                  <MenuItem value="kg">Kilogram</MenuItem>
                  <MenuItem value="oz">Ounce</MenuItem>
                  <MenuItem value="gram">Gram</MenuItem>
                </Select>
              </FormControl>
            </Grid>
            <Grid item xs={12} sm={4}>
              <TextField
                fullWidth
                label="Stock Quantity"
                type="number"
                value={newProduct.stockQuantity}
                onChange={(e) => setNewProduct({ ...newProduct, stockQuantity: parseInt(e.target.value) || 0 })}
                inputProps={{ min: 0 }}
              />
            </Grid>
            <Grid item xs={12} sm={4}>
              <TextField
                fullWidth
                label="Low Stock Threshold"
                type="number"
                value={newProduct.lowStockThreshold}
                onChange={(e) => setNewProduct({ ...newProduct, lowStockThreshold: parseInt(e.target.value) || 10 })}
                inputProps={{ min: 0 }}
              />
            </Grid>
            <Grid item xs={12} sm={4}>
              <TextField
                fullWidth
                label="Reorder Point"
                type="number"
                value={newProduct.reorderPoint}
                onChange={(e) => setNewProduct({ ...newProduct, reorderPoint: parseInt(e.target.value) || 5 })}
                inputProps={{ min: 0 }}
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setShowAddProduct(false)}>Cancel</Button>
          <Button
            onClick={handleAddProduct}
            variant="contained"
            disabled={!newProduct.name || !newProduct.price}
          >
            Add Product
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default Inventory;